<template>
   <section class="flexCenter main-page">
      
      <ul :key="project" class="flexCenter flow">
         
         <Project v-for="project in projects" :key="project.id"
            :project="project"
         />
      </ul>
      
      <RotatingPuzzle class="second"/>
      <LoadingPuzzle class="second"/>

   </section>
</template>


<script>
   import Project from "./Project"
   import RotatingPuzzle from "./RotatingPuzzle.vue"
   import LoadingPuzzle from "./LoadingPuzzle.vue"

   export default {
      name: "MainPage",

      components: {
         Project,
         RotatingPuzzle,
         LoadingPuzzle,
      },

      data() {
         return {
            project: {},
            projects: {},
         }
      },

      beforeMount() {
         this.getProjects();
      },

      methods: {
         async getProjects() {
            const response = await fetch("./projects.json")
            .then(data => { return data.json() });
            this.projects = response.sort().reverse();
         },
      },
   }
</script>


