<template>
    <section class="puzzle-container">
        
        <div class="flexCenter puzzle">
            <div class="circle lvl-1"></div>
            <div class="circle lvl-2"></div>
            <div class="circle lvl-3"></div>
            <div class="circle lvl-4"></div>
            <div class="circle lvl-5"></div>
            <div class="circle lvl-6"></div>
            <div class="circle lvl-7"></div>
        </div>

    </section>
</template>


<script>
    export default {
        name: "RotatingPuzzle",
    }
</script>


<style scoped lang="scss">
    
    $thickness: 30px;
    $sizeContainer: 800px;
    $sizeRing: 80px;
    .puzzle-container {
        overflow: hidden;
        position: absolute;
        bottom: 0;
        left: -$sizeContainer/2;
        height: $sizeContainer/2;
        width: $sizeContainer;
    }

    .puzzle {
        position: relative;
        height: 200%;
        border-radius: 50%;
    }

    .circle {
        position: absolute;
        border: solid $thickness transparent;
        border-radius: 50%;
        transform: rotateZ(-45deg);
    }
    

    $animDuration: 5s linear infinite;
    .lvl-1 {
        height: $sizeRing;
        width: $sizeRing;
        border-top-color: rgb(255, 217, 0);
        border-bottom-color: deeppink;

        animation: lvl_1 $animDuration;
        animation-delay: 0.8s;
    }

    .lvl-2 {
        height: $sizeRing * 2;
        width: $sizeRing * 2;
        border-top-color: lime;
        border-bottom-color: white;

        animation: lvl_2 $animDuration;
        animation-delay: 2.4s;
    }

    .lvl-3 {
        height: $sizeRing * 3;
        width: $sizeRing * 3;
        border-top-color: rgb(255, 217, 0);
        border-bottom-color: dodgerblue;

        animation: lvl_1 $animDuration;
        animation-delay: 1.1s;
    }

    .lvl-4 {
        height: $sizeRing * 4;
        width: $sizeRing * 4;
        border-top-color: lime;
        border-bottom-color: deeppink;

        animation: lvl_2 $animDuration;
        animation-delay: 3.3s;
    }

    .lvl-5 {
        height: $sizeRing * 5;
        width: $sizeRing * 5;
        border-top-color: rgb(255, 217, 0);
        border-bottom-color: white;

        animation: lvl_1 $animDuration;
        animation-delay: 1.8s;
    }

    .lvl-6 {
        height: $sizeRing * 6;
        width: $sizeRing * 6;
        border-top-color: lime;
        border-bottom-color: dodgerblue;

        animation: lvl_2 $animDuration;
        animation-delay: 1.5s;
    }

    .lvl-7 {
        height: $sizeRing * 7;
        width: $sizeRing * 7;
        border-top-color: rgb(255, 217, 0);
        border-bottom-color: darkviolet;

        animation: lvl_1 $animDuration;
        animation-delay: 3s;
    }


    // =====================================================================================================
    // Animations
    // =====================================================================================================
    @keyframes lvl_1 {
        0% { transform: rotateZ(-45deg) };
        100% { transform: rotateZ(315deg) };
    }

    @keyframes lvl_2 {
        0% { transform: rotateZ(-45deg) };
        100% { transform: rotateZ(-405deg) };
    }


</style>