<template>
    <li class="flexCenter project-frame" :id="project.id">
        
        <Pulse v-if="isPulsing"/>

        <figure class="flexCenter fig" @click="toggleProject()">
            <img :src="project.imageUrl" alt="photo du site web">
            <div class="flexCenter white-filter"></div>
        </figure>
        
        <div class="flexCenter project-caption">
            <h2>{{ project.title }}</h2>

            <span class="flexCenter created-at">
                <h3>Créé le :</h3>
                <p>{{ project.createdAt }}</p>
            </span>
            
            <span class="flexCenter description">
                <h3>Description :</h3>
                <p>{{ project.description }}</p>
            </span>
            
            <div v-show="project.skills" class="flexCenter skills">
                <h3>Compétences à valider :</h3>
                <li v-for="skill in skills" :key="skill">{{ skill }}</li>
            </div>
        </div>

        <button class="flexCenter btn green-btn switch-to-btn" @click="goToPage()">
            <i class="fas fa-sign-in-alt"></i>
            Basculer
        </button>

    </li>
</template>


<script>
    import Pulse from "./Pulse.vue"

    export default {
        name: "Project",

        components: {
            Pulse,
        },

        props: {
            project: Object,
        },

        data() {
            return {
                skills: "",
                isProjectOpen: false,
                isPulsing: false,
            }
        },

        beforeMount() {
            this.skills = this.project.skills;
        },

        methods: {
            toggleProject() {
                const project = document.getElementById(this.project.id);
                const figure = project.querySelector(".fig");
                const caption = project.querySelector(".project-caption");
                const btn = project.querySelector(".switch-to-btn");
                const filter = project.querySelector(".white-filter");

                if(!this.isProjectOpen) {
                    project.classList.remove("close-project");
                    figure.classList.remove("close-fig");
                    caption.classList.remove("close-caption");
                    btn.classList.remove("close-btn");

                    project.classList.add("open-project");
                    figure.classList.add("open-fig");
                    caption.classList.add("open-caption");
                    btn.classList.add("open-btn");

                    filter.style = "display: none";

                    this.isProjectOpen = true;
                    this.isPulsing = true;
                }
                
                else {
                    project.classList.remove("open-project");
                    figure.classList.remove("open-fig");
                    caption.classList.remove("open-caption");
                    btn.classList.remove("open-btn");

                    project.classList.add("close-project");
                    figure.classList.add("close-fig");
                    caption.classList.add("close-caption");
                    btn.classList.add("close-btn");

                    setTimeout(() => filter.style = "display: flex", 1500);

                    this.isProjectOpen = false;
                    this.isPulsing = false;
                }
            },

            goToPage() {
                window.open(this.project.href);
            },
        },
    }
</script>


<style scoped lang="scss">

    $marginBase: 20px;
    $projectHeigth: 270px;
    $figHeigth: 155px;
    $captionHeigth: 55px;
    $btnTranslate: 120%;
    
    .project-frame {
        position: relative;
        justify-content: space-between;
        overflow: hidden;
        height: $projectHeigth;
        width: 80%;
        margin: 20px;
        margin-top: 0;
        border-radius: $marginBase + 10px;
        background-color: dodgerblue;
        background: linear-gradient(to right, lightblue, dodgerblue, lightblue);
    }

    
    /* ========== Picture ========== */
    .project-frame figure {
        position: relative;
        overflow: hidden;
        height: $figHeigth;
        width: 100%;
        margin: $marginBase;
        margin-bottom: 0;
        border-radius: $marginBase;
    }

    .project-frame img {
        cursor: pointer;
        object-fit: cover;
        height: 100%;
        width: 100%;
        background: white;
    }

    .white-filter {
        cursor: pointer;
        position: absolute;
        height: 100%;
        width: 0;
        opacity: 0;
        background: black;
    }
    

    $titleWidth: 60%;
    
    /* ========== Caption ========== */
    .project-caption {
        justify-content: space-between;
        overflow: hidden;
        height: $captionHeigth;
        margin: $marginBase;
        padding-left: $marginBase;
        padding-right: 10px;
        border-radius: $marginBase;
        background: linear-gradient(to bottom right, rgb(130, 130, 130), rgb(245, 245, 245), rgb(110, 110, 110));
    }


    $marginTopBase: 5px;
    .project-caption h2 {
        margin-top: 15px;
        margin-bottom: 10px;
        width: $titleWidth;
    }

    .created-at {
        width: 100% - $titleWidth;
    }

    .description,
    .skills {
        justify-content: flex-start;
    }

    .created-at h3,
    .description h3,
    .skills h3 {
        margin: 0;
        margin-top: $marginTopBase;
        margin-right: 10px;
        font-size: 110%;
        font-weight: 400;
        text-decoration-line: underline;
        text-underline-offset: 2px;
        text-decoration-thickness: 2px;
    }

    .created-at p,
    .description p {
        margin: 0;
        margin-top: $marginTopBase;
        line-height: 120%;
        font-size: 100%;
    }
    

    $paddingSide: 40px;
    /* ========== Skills List ========== */
    .skills {
        padding-left: $paddingSide;
        margin-bottom: $marginTopBase + 5px;
    }

    .skills h3 {
        width: 100%;
        margin-left: - $paddingSide;
    }

    .skills li {
        list-style-type: disc;
        width: 45%;
        margin-top: 8px;
        font-size: 100%;
    }


    /* ========== Buttons ========== */
    .switch-to-btn {
        cursor: pointer;
        justify-content: space-around;
        height: 80px;
        width: 25%;
        margin: $marginBase;
        transform: translateX($btnTranslate);
    }

    .fa-sign-in-alt {
        font-size: 140%;
    }


    $delay: 0.5s;
    // ========= Transition Class =========
    .open-project {
        height: 667px;
        width: 100%;
    }

    .open-fig {
        height: 393px !important;
    }

    .open-caption {
        height: 215px;
        width: 64%;
        margin-right: 0;
    }

    .open-btn {
        transform: translateX(0%);
        transition-delay: $delay + 0.3;
    }



    .close-project {
        height: $projectHeigth;
        transition-delay: $delay;
    }

    .close-fig {
        height: $figHeigth;
        transition-delay: $delay;
    }

    .close-caption {
        height: $captionHeigth;
        transition-delay: $delay;
    }

    .close-btn {
        transform: translateX($btnTranslate);
    }

    .open-project,
    .open-fig,
    .open-caption,
    .open-btn,
    .close-project,
    .close-fig,
    .close-caption,
    .close-btn {
        transition-duration: 1s;
    }


    .fig:hover > .white-filter {
        opacity: 50%;
        width: 100%;
        transition-duration: 0.5s;
    }
</style>